import React from "react";

import { Cell, CellTitle } from "../components/Cell";
import { List, ListItem } from "../components/List";
import Layout from "../components/Layout";
import About from "../components/About";
import { GithubProfileUrl } from "../consts";

const Index = () => (
    <Layout>
        <About/>

        <Cell>
            <CellTitle>I made some games</CellTitle>

            <List>
                <ListItem>
                    <a href="/nsf">Not So Flat</a> in which you are flat, but not entirely.
                </ListItem>

                <ListItem>
                    <a href="/cubic-roll">Cubic Roll</a> featuring a rolling cube.
                </ListItem>

                <ListItem>
                    <a href="/line-bound">Line Bound</a> where a line is stuck in a confined space.
                </ListItem>

                <ListItem>
                    <a href="/uncopy">Uncopy</a> where similar stuff annihilates.
                </ListItem>

                <ListItem>
                    <a href="/rebus">Ре:бусы</a> about solving Russian rebuses, mostly.
                </ListItem>
            </List>
        </Cell>

        <Cell>
            <CellTitle>I also made this other stuff</CellTitle>

            <List>
                <ListItem>
                    <a href={`${GithubProfileUrl}/solo`} target="_blank">Solo</a>,

                    a simple game engine with OpenGL/Vulkan backend, Bullet physics and Lua scripting.
                </ListItem>

                <ListItem>
                    <a href="/wireframe">Wireframe</a> shader for Unity, based on geometry shaders.
                </ListItem>

                <ListItem>
                    <a href="http://u3d.as/3eH" target="_blank">Unity Hardware Cursor</a>, a lag-free, 
                    OS-rendered custom mouse cursor for Unity. No longer relevant since
                    this feature is now included in the engine.
                </ListItem>

                <ListItem>
                    <a href={`${GithubProfileUrl}/ustats`} target="_blank">Ustats NGINX module</a> for
                    collecting upstream servers statistics.
                </ListItem>

                <ListItem>
                    <a href={GithubProfileUrl} target="_blank">More on Github</a>.
                </ListItem>
            </List>
        </Cell>
    </Layout>
);

export default Index;
